.header-container {
  max-width: 320px;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
}

.header {
  height: 230px;
  opacity: .9;
  background-color: #303030;
  background-image: linear-gradient(rgba(0, 0, 0, .56), rgba(0, 0, 0, .56)), url("main-header-background-mobile@1x.e1135d81.jpg");
  background-position: 20%;
  background-repeat: no-repeat;
  background-size: cover;
