.logo {
  align-items: center;
  gap: 12px;
  display: inline-flex;
}

.logo:hover, .logo:focus {
  animation: ScaleBig 2s linear;
}

@keyframes ScaleBig {
  0% {
    transform: scale(100%);
  }

  50% {
    transform: scale(120%);
  }

  100% {
    transform: scale(100%);
  }
}

.logo-title {
  font-family: Roboto, sans-serif;
  font-size: 30px;
  font-weight: 500;
  line-height: 1.16;
  display: none;
}

.nav {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.nav-list {
  gap: 30px;
  display: flex;
}

.link {
  color: #fff;
  cursor: pointer;
}

.nav-link {
  padding: 3px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.16px;
  position: relative;
}

.nav-title {
  padding: 3px;
}

.nav-link:before, .nav-link:after {
  content: "";
  width: 0;
  height: 2px;
  background: #fff;
  transition: all .2s linear;
  position: absolute;
}

.nav-title:before, .nav-title:after {
  content: "";
  width: 2px;
  height: 0;
  background: #fff;
  transition: all .2s linear;
  position: absolute;
}

.nav-link:hover:before, .nav-link:hover:after {
  width: 100%;
}

.nav-link:hover .nav-title:before, .nav-link:hover .nav-title:after {
  height: 100%;
}

.nav-link:after {
  transition-duration: .4s;
  bottom: 0;
  left: 0;
}

.nav-link .nav-title:after, .nav-link:before {
  transition-duration: .4s;
  top: 0;
  right: 0;
}

.nav-link .nav-title:before {
  transition-duration: .4s;
  bottom: 0;
  left: 0;
}

.nav .nav-link.current-link:after {
  content: "";
  width: 100%;
  height: 3px;
  background-color: #ff001b;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.search-form {
  width: 280px;
  margin: 60px auto 0;
  position: relative;
}

.search-input {
  width: 100%;
  color: #fff;
  background-color: rgba(0, 0, 0, 0);
  border: 1px rgba(0, 0, 0, 0);
  border-bottom: 1px solid #fff;
  outline: none;
  transition: all .2s linear;
  display: block;
}

.search-input::-ms-input-placeholder {
  color: #fff;
  transition: all .2s linear;
}

.search-input::placeholder {
  color: #fff;
  transition: all .2s linear;
}

.search-form:hover .search-input, .search-form:focus .search-input {
  border-color: #ff6b01;
}

.search-form:hover .search-input::-ms-input-placeholder, .search-form:focus .search-input::-ms-input-placeholder {
  color: #ff6b01;
}

.search-form:hover .search-input::placeholder, .search-form:focus .search-input::placeholder {
  color: #ff6b01;
}

.search-icon {
  stroke: #fff;
  transition: all .2s linear;
}

.search-form:hover .search-icon {
  stroke: #ff6b01;
}

.submit-button {
  background-color: rgba(0, 0, 0, 0);
  border: none;
  outline: none;
  position: absolute;
  bottom: 0;
  right: 0;
}

